<template>
    <div>
        <el-table
                :data="currencies"
                stripe
                style="width: 100%">
            <el-table-column
                    prop="currency_id"
                    label="ID">
            </el-table-column>
            <el-table-column
                    prop="currency_type"
                    label="货币代号">
            </el-table-column>
            <el-table-column
                    prop="currency_er"
                    label="系统汇率">
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "Currency",
        data() {
            return {
                msg: 'Hello Vue',
                currencies: []
            }
        },
        created() {
            const _this = this
            axios.get('http://localhost/getCurrency').then(function (resp) {
                _this.currencies = resp.data
                // console.log(resp)
            })
        }
    }
</script>
