<template>
    <div>
<!--        <el-button type="primary">充值账户余额</el-button>-->

        <el-button type="primary" @click="dialogFormVisible = true">充值余额</el-button>

        <el-dialog title="充值余额" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                <el-form-item label="充值金额" :label-width="formLabelWidth">
                    <el-input v-model="form.value" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="用户密码" :label-width="formLabelWidth">
                    <el-input v-model="form.password" autocomplete="off" type="password"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="charge">确 定</el-button>
            </div>
        </el-dialog>

        <el-table
                :data="entities"
                stripe
                style="width: 100%">
            <el-table-column
                    prop="entity_id"
                    label="ID"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="entity_name"
                    label="实体名称"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="currency_id"
                    label="货币代号"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="currency_amount"
                    label="账户余额"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="currency_yesterday_stm"
                    label="昨日清算结果"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="public_key"
                    label="公钥">
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "Entity",
        data() {
            return {
                msg: 'Hello Vue',
                entities: [],
                dialogFormVisible: false,
                form: {
                    value: '',
                    password: ''
                }
            }
        },
        created() {
            const _this = this
            axios.get('http://localhost/getEntityById').then(function (resp) {
                _this.entities = resp.data
                // console.log(resp)
            })
        },
        methods: {
            charge() {
                this.dialogFormVisible = false
                let _this = this
                let data = {
                        value: this.form.value,
                        password: this.form.password
                }
                console.log(this.form)
                axios.post("http://localhost/entityCharge", data).then(function (resp) {
                    _this.chargeSuccess();

                })
            },
            chargeSuccess() {
                this.$message({
                    message: '充值成功！',
                    type: 'success'
                });
            }
        }
    }
</script>

