<template>
  <div>
      <el-table
              :data="entities"
              stripe
              style="width: 100%">
          <el-table-column
                  prop="entity_id"
                  label="ID"
                  width="150">
          </el-table-column>
          <el-table-column
                  prop="entity_name"
                  label="实体名称"
                  width="150">
          </el-table-column>
          <el-table-column
                  prop="currency_id"
                  label="货币代号"
                  width="150">
          </el-table-column>
          <el-table-column
                  prop="public_key"
                  label="公钥">
          </el-table-column>
      </el-table>
  </div>
</template>

<script>
    export default {
        name: "Entity",
        data() {
            return {
                msg: 'Hello Vue',
                entities: []
            }
        },
        created() {
            const _this = this
            axios.get('http://localhost/getEntity').then(function (resp) {
                _this.entities = resp.data
                // console.log(resp)
            })
        }
    }
</script>
