import Vue from 'vue'
import VueRouter from 'vue-router'
import Transaction from '../views/Transaction'
import Entity from '../views/Entity'
import Index from '../views/index'
import Currency from '../views/Currency'
import AcTransaction from "../views/AcTransaction";
import NotAcTransaction from "../views/NotAcTransaction";
import EntityInfo from "../views/EntityInfo";

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: '交易',
    component: Index,
    redirect: "/settlement",
    children: [
      {
        path: '/settlement',
        name: '所有交易记录',
        component: Transaction
      },
      {
        path: '/AcSettlement',
        name: '已完成交易记录',
        component: AcTransaction
      },
      {
        path: '/NotAcSettlement',
        name: '未完成交易记录',
        component: NotAcTransaction
      }
    ]
  },
  {
    path: '/',
    name: '实体',
    component: Index,
    children: [
      {
        path: '/EntityInfo',
        name: '当前实体信息',
        component: EntityInfo
      },
      {
        path: '/entity',
        name: '其他实体信息',
        component: Entity
      }
    ]
  },
    {
    path: '/',
    name: '货币',
    component: Index,
    children: [
      {
        path: '/currency',
        name: '货币信息',
        component: Currency
      }
    ]
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
