<template>
    <div>
        <el-table
                :data="transactions"
                style="width: 100%"
                :row-class-name="tableClass">
            <el-table-column
                    prop="transaction_id"
                    label="ID"
                    width="80">
            </el-table-column>
            <el-table-column
                    prop="transaction_from_entity_id"
                    label="支付方实体ID"
                    width="80">
            </el-table-column>
            <el-table-column
                    prop="transaction_from_entity_account"
                    label="支付方账号"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="transaction_to_entity_id"
                    label="收款方实体ID"
                    width="80">
            </el-table-column>
            <el-table-column
                    prop="transaction_to_entity_account"
                    label="收款方账号"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="transaction_currency_amount"
                    label="交易金额"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="transaction_currency_fee"
                    label="手续费"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="transaction_currency_id"
                    label="交易货币ID"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="transaction_send_time"
                    label="交易发送时间"
                    width="160">
            </el-table-column>
            <el-table-column
                    prop="transaction_time"
                    label="交易接受时间"
                    width="160">
            </el-table-column>
            <el-table-column
                    prop="transaction_sign"
                    label="支付方交易签名"
                    width="">
            </el-table-column>
            <el-table-column
                    prop="transaction_ac_sign"
                    label="收款方交易签名"
                    width="">
            </el-table-column>
        </el-table>

        <!--      <el-pagination-->
        <!--              background-->
        <!--              layout="prev, pager, next"-->
        <!--              page-size="10"-->
        <!--              :total="50"-->
        <!--              @current-change="page">-->
        <!--      </el-pagination>-->
    </div>
</template>

<script>
    export default {
        name: "Transaction",
        methods: {
            tableClass({row}) {
                if (row.transaction_ac_sign != null) {
                    return 'success-row';
                } else
                if (row.transaction_from_entity_id == this.user){
                    return 'alert-row';
                } else
                    return 'warning-row';
                return '';
            }
        },
        data() {
            return {
                msg: 'Hello Vue',
                user: 0,
                transactions: []
            }
        },
        created() {
            const _this = this
            axios.get('http://localhost/getTransaction').then(function (resp) {
                _this.transactions = resp.data
                // console.log(resp)
            })
            axios.get('http://localhost/getTransactionUser').then(function (resp) {
                _this.user = resp.data

            })
        }
    }
</script>

<style>
    .el-table .alert-row {
        background: #fdf5e6;
    }

    .el-table .success-row {
        background: #f0f9eb;
    }

    .el-table .warning-row {
        background: #f9ebeb;
    }
</style>